import React from 'react';
import { alertBox, switchLoader } from '../../commonRedux/';
import WalletMenu from '../../components/Menu/WalletMenu';
import WalletMenuMobile from '../../components/Menu/WalletMenuMobile';
import Spinner from '../../components/Spinner';
import TabsUI from '../../components/Tabs/index';
import WalletAllBalance from '../../components/Wallet/allBalance';
import config from "../../config/index";
import walletCheck from '../../hooks/walletCheck';
import { getUploadedFiles, removeUploadedFile } from '../../http/http-calls';
import { saveKYC } from '../../http/wallet-calls';
import Modal from '../../components/Popup';
import './styles.css';
import Button from '../../components/Button';
import NavigationBlocker from '../../components/NavigationBlocker';

class WalletVerification extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            open:false,
            attempt:0,
            doc1:null,
            doc2:null,
            doc3:null,
            doc4:null,
            loading : true,
            status: '',
            content : 'loading',
            currentTab: 0,
            file: '',
            selected: [],
            files: [],

        };
    }

    componentDidMount = () =>{
        // this.uploadedFiles();
        this.checkWallet();
    };
    componentWillUnmount = () =>{
        window.onbeforeunload = null
        this.setState({
            open:false
        })
    }
    closeModal = () =>{
        this.setState({open: false});
    }
    leave = () => {
        this.setState({
            open:false,
            doc1:null,
            doc2:null,
            doc3:null,
            doc4:null
        })
    }
    
    checkWallet = () => {
        
        walletCheck().then(resp=>{
            if(resp.data.status == 'Accept'){
            this.setState({
                loading: false,
                //status : (resp.data.status == undefined) ? '' : resp.data.status,
                status: resp.data.status,
                doc1: (resp.data.idprooffront == undefined || resp.data.idprooffront == "") ? null : resp.data.idprooffront,
                doc2: (resp.data.idproofback == undefined || resp.data.idproofback == "") ? null : resp.data.idproofback,
                doc3: (resp.data.idproofdoc == undefined || resp.data.idproofdoc == "") ? null : resp.data.idproofdoc,
                doc4: (resp.data.bankproof == undefined || resp.data.bankproof == "") ? null : resp.data.bankproof,
            }, () => {
                window.localStorage.setItem("kycStatus", resp.data.status);
                window.localStorage.setItem("doc1", this.state.doc1);
                window.localStorage.setItem("doc2", this.state.doc2);
                window.localStorage.setItem("doc3", this.state.doc3);
                window.localStorage.setItem("doc4", this.state.doc4);
            });   
        }else{
            this.setState({
                loading: false
            })
        }
        }, err => {
            this.setState({
                loading:false,
                error: 'Authentication Error!'
            })
        })
    };

    uploadedFiles = () => {
        getUploadedFiles({page: "walletKYC"}).then(async resp => {
            this.setState({
                files : resp.files
            });
        }, error => {
            
        });
    };

    removeFile = (file) => {
        switchLoader(true, 'File is being removed, please wait...');
        removeUploadedFile(file).then(async resp => {
            this.uploadedFiles();
            switchLoader();
        }, error => {
            
            alertBox(true, error.message);
            switchLoader();
        });
    };

    changeTab = (newValue) => {
        // if((this.state.doc1 != null || this.state.doc2 != null  || this.state.doc3 != null  || this.state.doc4 != null) && this.state.status == ''){
        //     this.setState({open:false});
        // }else{
        this.setState({
            open:false,
            currentTab: newValue
        });
        // }
    };

     browse = (e) => {
        var target = e.target.getAttribute("data-target");
        console.log(target);
        var input = document.getElementById(target);
        input.click();
     };

     chooseFile = (e) => {
        // var temp = this.state.files;
        // temp[i]['selected'] = (temp[i]['selected']) ? false : true;
        // this.setState({
        //     files: temp
        // });
     };
     viewDocument = (e) => {
        var file = this.state[e];
        if(file instanceof File){
            window.open(URL.createObjectURL(file));
        }else{
            window.open(config.walletDocUrl+file);
        }
     };

     clearFile = (e) => {
        var input = e.target;
        var id = input.getAttribute("data-target");
        this.setState({
            [id] : null
        }, ()=>{
            if((this.state.doc1 != null || this.state.doc2 != null  || this.state.doc3 != null  || this.state.doc4 != null) && this.state.status == ''){
                this.setState({open:true});
            }else{
                this.setState({open:false})
            }
        });
     };
     
     inputChange = (e) => {
    
        var input = e.target;
        var id = input.getAttribute("id");
        console.log(input.files[0])
        var file = input.files[0];
        if(!file.type.includes("image/") && file.type != "application/pdf"){
            alertBox(true, "PDF/Image files only allowed");
        }
        else if(file.size > 1048576){
            alertBox(true, "File size should be lesser than or equal to 1 MB");
        }
        else{
            this.setState({
                [id] : input.files[0]
            }, ()=>{
                if((this.state.doc1 != null || this.state.doc2 != null  || this.state.doc3 != null  || this.state.doc4 != null) && this.state.status == ''){
                    this.setState({open:true});
                }else{
                    this.setState({open:false})
                }
            });
        }
        // switchLoader(true, 'File is being uploaded, please wait...');

        //     uploadFile({page: "walletKYC", file: input.files[0]}).then(async resp => {
        //         var temp = this.state.files;
        //         temp = [...temp, resp.file];
        //         this.setState({
        //             files : temp
        //         });
        //         switchLoader();
        //     }, error => {
        //         switchLoader();
        //         alertBox(true, error.message);
        //     });
        
     };
     blobify = (selected, url) => {
        return new Promise((resolve, reject) => {
            if(selected){
            var request = new XMLHttpRequest();
            request.open('GET', url, true);
            request.responseType = 'blob';
            request.onload = function() {
                resolve(request.response);
            };
            request.send();
            }else{
                resolve('');
            }
        });
    }
    submit = async () => {
        switchLoader(true, "Submitting, please wait...");
        // var selected = [];
        // var temp;
        // const fileReader = new FileReader();
        // var bar = new Promise(async (resolve, reject) => {
        //     this.state.files.forEach(async (value, index, array) => {
                
        //             this.blobify(value.selected, config.baseUrl+value.filePath).then(res => {
        //                 if(res != ''){ 
        //                     selected.push(res);
        //                 }
        //                 if (index === array.length-1) resolve();
        //             });
        //             //console.log(temp);
        //             //if(temp != ''){ 
                        
        //             //}
                
                
        //     });
        // });
        
        // bar.then(() => {
            if(this.state.doc1 == null || this.state.doc2 == null || this.state.doc3 == null || this.state.doc4 == null){
                switchLoader();
                alertBox(true, "Please select files for all categories!");
               
            }else{
                // var selected = [
                //     this.state.doc1,
                //     this.state.doc2,
                //     this.state.doc3,
                //     this.state.doc4
                // ];
                var data = {
                    Front: this.state.doc1,
                    Back: this.state.doc2,
                    Doc: this.state.doc3,
                    Bank: this.state.doc4
                }
                saveKYC(data).then(async resp => {
                    //console.log(resp.message);
                    if(resp.status == false){
                        alertBox(true, resp.message);
                    }
                    switchLoader();
                    this.setState({
                        status: "Pending"
                    }, this.changeTab(0));
                    //window.location.reload();
                }, error => {
                    switchLoader();
                    alertBox(true, error == undefined? "Someting went wrong" : error.message);
                });
            }
        // })
        
        
    };



    render() {

        return (
            <div className="container my-wall-container depositPage">
                {this.state.open && <NavigationBlocker navigationBlocked={this.state.open} /> }
                 {/* <Modal displayModal={this.state.open} closeModal={this.closeModal} >
                     <div>
                         <p>You have selected but haven't submitted your documents yet</p>
                         <p>Are you sure about leaving the page?</p>
                     </div>
                <div className="">
                <Button variant="primary" size="compact m-2" onClick={() => this.leave()}>Yes</Button>
                <Button variant="secondary" size="compact m-2" onClick={this.closeModal}>No</Button>
                </div>
             </Modal> */}
                <div className="row mt-2">

                    {/* <!-- left column --> */}
                    <div className="col-sm empty-container-with-out-border left-column">
                        <WalletMenu {...this.props} />
                    </div>
                    {/* <!-- end left column --> */}

                    {/* <!-- center column --> */}
                    <div className="col-sm empty-container-with-border center-column">
                    <WalletMenuMobile {...this.props} />
                    <TabsUI tabs={['Verification Status', 'Manage Documents']} type="transactions" className="noBorder" currentTab={this.state.currentTab} preventChange={this.state.open} changeTab={this.changeTab} />
                    {
                        this.state.currentTab == 0 ? 
                        <div className="tab row m--1 mt-2">
                                <div className="bordered p-4 w-100">
                                 {
                                     this.state.loading ? <Spinner /> :

                                     this.state.status == '' ? 
                                     <p className="text-center text-danger col-md-6 mx-auto p-3">Your account hasn't been verified yet, please go to "Manage Documents" tab and upload your documents and wait for approval!</p>
                                     :
                                     this.state.status == 'Pending' ?
                                     <p className="text-center text-warning col-md-6 mx-auto p-3">Your have already submitted your documents, please wait for approval</p>
                                     :
                                     this.state.status == 'Reject' ?
                                     <p className="text-center text-danger col-md-6 mx-auto p-3">You account verification has been rejected, please resubmit it again!</p>
                                     :
                                     <p className="text-center text-success col-md-6 mx-auto p-3">You account has been verified, now you can use all wallet features!</p>
                                 }
                                </div>

                        </div>
                        
                        :
                        <div className="tab mt-3 p-2 bordered">
                           {/* <div className="form-group">
                                <div className="d-flex justify-content-start align-items">
                                    <input className="form-control" type="text" placeholder="jpg, png, jpeg, pdf files only" data-target="file" onClick={(e) => this.browse(e)} />
                                    <input type="file" name="file" className="hide" id="file" onChange={(e) => this.inputChange(e)} />
                                    <button className="btn btn-main box" data-target="file" type="button" onClick={(e) => this.browse(e)}>Browse</button>
                                </div>
                            </div> */}
                            <div className="cards">
                            <div className={"w50 card"+( this.state.doc1 != null ? " bg-main": " bg-light")}>
                                <h4>RG, CNH or Passport - Front</h4>
                                { this.state.doc1 != null &&  <a className="text-dark" data-target="doc1" onClick={(e) => this.viewDocument('doc1')} target="_BLANK" ><span className="fa fa-download"></span> <span className="text-main">View Document</span></a> }
                                {
                                this.state.doc1 == null ?  <button className="btn btn-main box cardAction" data-target="doc1" onClick={(e) => this.browse(e)}>Choose</button>
                                :
                                this.state.status == '' || this.state.status == 'Reject' ?
                                <button className="btn btn-main box cardAction" data-target="doc1" onClick={(e) => this.clearFile(e)}>Clear</button>
                                :
                                null
                                }
                                <input type="file" name="doc1" className="hide" id="doc1" onChange={(e) => this.inputChange(e)}  accept="image/*, application/pdf" />
                            </div>
                            <div className={"w50 card"+( this.state.doc2 != null ? " bg-main": " bg-light")}>
                                <h4>RG, CNH or Passport - Back</h4>
                                { this.state.doc2 != null &&  <a className="text-dark" data-target="doc2" onClick={(e) => this.viewDocument('doc2')} target="_BLANK" ><span className="fa fa-download"></span> <span className="text-main">View Document</span></a> }
                                {
                                this.state.doc2 == null ?  <button className="btn btn-main box cardAction" data-target="doc2" onClick={(e) => this.browse(e)}>Choose</button>
                                :
                                this.state.status == '' || this.state.status == 'Reject' ?
                                <button className="btn btn-main box cardAction" data-target="doc2" onClick={(e) => this.clearFile(e)}>Clear</button>
                                :
                                null
                                }
                                <input type="file" name="doc2" className="hide" id="doc2" onChange={(e) => this.inputChange(e)} accept="image/*, application/pdf" />
                            </div>
                            <div className={"w50 card"+( this.state.doc3 != null ? " bg-main": " bg-light")}>
                                <h4>Selfie with Document</h4>
                                { this.state.doc3 != null &&  <a className="text-dark" data-target="doc3" onClick={(e) => this.viewDocument('doc3')} target="_BLANK" ><span className="fa fa-download"></span> <span className="text-main">View Document</span></a> }
                                {
                                this.state.doc3 == null ?  <button className="btn btn-main box cardAction" data-target="doc3" onClick={(e) => this.browse(e)}>Choose</button>
                                :
                                this.state.status == '' || this.state.status == 'Reject' ?
                                <button className="btn btn-main box cardAction" data-target="doc3" onClick={(e) => this.clearFile(e)}>Clear</button>
                                : null
                                }
                                <input type="file" name="doc3" className="hide" id="doc3" onChange={(e) => this.inputChange(e)} accept="image/*, application/pdf" />
                            </div>
                            <div className={"w50 card"+( this.state.doc4 != null ? " bg-main": " bg-light")}>
                                <h4>Proof of financial capability - Bank Statement / Income Tax / Income Proof.</h4>
                                { this.state.doc4 != null &&  <a className="text-dark" data-target="doc4" onClick={(e) => this.viewDocument('doc4')} target="_BLANK" ><span className="fa fa-download"></span> <span className="text-main">View Document</span></a> }
                                {
                                this.state.doc4 == null ?  <button className="btn btn-main box cardAction" data-target="doc4" onClick={(e) => this.browse(e)}>Choose</button>
                                :
                                this.state.status == '' || this.state.status == 'Reject' ?
                                <button className="btn btn-main box cardAction" data-target="doc4" onClick={(e) => this.clearFile(e)}>Clear</button>
                                :null
                                }
                                <input type="file" name="doc4" className="hide" id="doc4" onChange={(e) => this.inputChange(e)} accept="image/*, application/pdf" />
                            </div>
                            {/* {
                                this.state.files.length > 0 && this.state.files.map((e, i) => {
                                    return <div className={"card"+(e.selected ? " bg-main": " bg-light")}>
                                        <span className="fa fa-times remove" onClick={() => this.removeFile(e) }></span>
                                <h4>{e.fileName}</h4>
                                        <a className="text-dark" href={config.baseUrl+e.filePath} target="_BLANK" ><span className="fa fa-download"></span> <span className="text-main">View Document</span></a>
                                        <button className="btn btn-main box cardAction" onClick={() => this.chooseFile(e, i)}>{e.selected ? 'Clear' : 'Choose'}</button>
                                    </div>
                                
                                })
                            } */}
                           </div>
                           {
                               (this.state.status == null || this.state.status == '' || this.state.status == 'Reject') && <div className="form-group mt-80 d-flex align-items-center justify-content-center">
                               <button className="btn btn-main box big" onClick={this.submit}>Submit for Approval</button>
                           </div>
                           }
                            
                        </div>
                    }
                    </div>
                    {/* <!-- end center column --> */}
                    <div className="col-sm empty-container-with-out-border right-column">
                    <WalletAllBalance />
                    </div>
                </div>
            </div>
        );
    }
}

export default WalletVerification;
